.root {
    padding: 0.4em 0.2em;
}

.item {
    float: left;
    margin: 0.4em;
    text-decoration: none;
    width: 14em;
}

.card {
    background-color: #5c6c7c;
    border: 1px solid #5c6c7c;
    -webkit-box-shadow: black 3px 3px;
            box-shadow: black 3px 3px;
    color: lightgray;
    padding-top: 0.75em;
}

.card:hover {
    border: 1px solid white;
    color: white;
}
